import React from "react";

import Home from "../pages/home";
import Register from "../pages/register";
import About from "../pages/about";
import Query from "../pages/query";
import Login from "../pages/login";

export default [
  {
    path: "/",
    element: <Home />,
    meta: {
      title: "SAI",
    },
  },
  {
    path: "/register",
    element: <Register />,
  },
  {
    path: "/about",
    element: <About />,
  },
  {
    path: "/queryList",
    element: <Query />,
  },
  {
    path: "/login",
    element: <Login />,
  },
];
