import React from "react";

export default function Tr(props) {
  const info = { ...props.info };
  const { language } = info;
  return (
    <tr>
      <th>{props.id}</th>
      <td>{props.name}</td>
      <td>{props.email}</td>
      <td>{props.qq}</td>
      <td>{props.introduce}</td>
      <td>{info.level === 0 ? "初学" : info.level === 50 ? "熟练" : "精通"}</td>
      <td>{info.direction}</td>
      <td className=" whitespace-nowrap">
        {language.includes("c") ? (
          <div className="badge bg-red-400">cpp</div>
        ) : (
          ""
        )}
        {language.includes("java") ? (
          <div className="badge bg-blue-400">java</div>
        ) : (
          ""
        )}
        {language.includes("java") ? (
          <div className="badge bg-green-600">python</div>
        ) : (
          ""
        )}
        {info.other !== "" ? <div className="badge">{info.other}</div> : ""}
      </td>
      <td>{props.management ? "是" : "否"}</td>
    </tr>
  );
}
