import React, { Component } from "react";

class Register extends Component {
  componentDidMount() {
    document.title = "报名-SAI创新基地";
  }

  updateURL = "/submit";
  nameReg = /^[\u4e00-\u9fa5]{0,}$/;
  idReg = /^2[0-2]0[0-1][0-9][1-5]0[1-5][0-6][0-9]$/;
  qqReg = /^[1-9][0-9]{4,10}$/;
  emailReg = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;

  state = {
    form: {
      name: "",
      id: "",
      email: "",
      qq: "",
      introduce: "",
      management: false,
      code: {
        level: 0,
        direction: "软件",
        language: {
          c: false,
          java: false,
          python: false,
          else: "",
        },
      },
    },
    submited: false,
    s: false,
  };

  submit = () => {
    const { s } = this.state;
    if (s) return;
    this.setState({ ...this.state, s: true });
    setTimeout(() => {
      this.setState({ ...this.state, s: false });
    }, 2000);
    if (this.state.submited) {
      this.alertTitle.innerText = "已成功报名";
      this.alertForm("请勿重复提交");
      return;
    }
    this.alertTitle.innerText = "还有未填完的内容";
    const { name, id, email, qq, introduce, elseLang } = this;
    const {
      form: {
        code: { language },
        code,
      },
    } = this.state;
    if (name.value === "") this.alertForm("姓名");
    else if (!this.nameReg.test(name.value)) {
      this.alertTitle.innerText = "名字格式不正确";
      this.alertForm("请重新输入名字");
    } else if (id.value === "") this.alertForm("学号");
    else if (!this.idReg.test(id.value)) {
      this.alertTitle.innerText = "学号格式不正确";
      this.alertForm("请重新输入学号");
    } else if (email.value === "") this.alertForm("邮箱");
    else if (!this.emailReg.test(email.value)) {
      this.alertTitle.innerText = "邮箱格式不正确";
      this.alertForm("请重新输入邮箱");
    } else if (qq.value === "") this.alertForm("QQ");
    else if (!this.qqReg.test(qq.value)) {
      this.alertTitle.innerText = "QQ格式不正确";
      this.alertForm("请重新输入QQ");
    } else if (introduce.value === "") this.alertForm("自我介绍");
    else {
      this.setState(
        {
          ...this.state,
          form: {
            ...this.state.form,
            name: name.value,
            id: id.value,
            email: email.value,
            qq: qq.value,
            introduce: introduce.value,
            code: {
              ...code,
              language: {
                ...language,
                else: elseLang.value,
              },
            },
          },
        },
        () => {
          this.submiting();
        }
      );
    }
  };

  submiting = () => {
    const {
      form,
      form: {
        code,
        code: { language },
      },
    } = this.state;
    let lang = [];
    if (language.c) lang.push("c");
    if (language.java) lang.push("java");
    if (language.python) lang.push("python");
    let subData = {
      name: form.name,
      id: form.id,
      email: form.email,
      qq: form.qq,
      introduce: form.introduce,
      management: form.management,
    };
    // delete subData.code;
    subData.info = {
      level: code.level,
      direction: code.direction,
      language: lang,
      other: language.else,
    };
    // console.log(subData);
    fetch(this.updateURL, {
      method: "POST",
      body: JSON.stringify(subData),
      headers: new Headers({
        "Content-Type": "application/json",
      }),
    })
      .then((res) => {
        // res.text()
        if (res.status === 200) {
          this.alertTitle.innerText = "提交成功";
          this.alertForm("请等候考核通知");
          // post成功后回调
          this.submitSucessed();
        } else {
          this.alertForm("网络出现问题，请稍后重试");
        }
      })
      .catch((err) => {
        console.log(err);
      });
    // .then(
    //     res => {
    //         console.log(res)
    //         if (res === '提交成功') {
    //             this.alertTitle.innerText = '提交成功';
    //             this.alertForm('请等候考核通知');
    //             // post成功后回调
    //             this.submitSucessed();
    //         }
    //         else {
    //             this.alertTitle.innerText = '错误';
    //             if (res.search('unique')) {
    //                 console.log(res);
    //                 this.alertForm('网络问题,请联系管理员');
    //             }
    //             else this.alertForm(res);
    //         }
    //     }
    // ).catch(err=>{this.alertForm("网络出现问题，请稍后重试")})
  };

  submitSucessed = () => {
    this.setState({
      ...this.state,
      submited: true,
    });
  };

  alertForm = (item) => {
    this.alertInfo.innerText = item;
    this.alertBox.click();
  };

  changeCodeState = (item) => {
    return (e) => {
      const {
        form: { code },
        form,
      } = this.state;
      this.setState({
        form: {
          ...form,
          code: {
            ...code,
            [item]: e.target.value,
          },
        },
      });
    };
  };

  changeCodeLanguage = (item) => {
    return (e) => {
      // console.log(e.target.value);
      const {
        form: {
          code: { language },
          code,
        },
        form,
      } = this.state;
      const i = language[item];
      this.setState({
        form: {
          ...form,
          code: {
            ...code,
            language: {
              ...language,
              [item]: !i,
            },
          },
        },
      });
    };
  };

  render() {
    const {
      form: {
        code: { level, direction },
        code,
      },
      form,
    } = this.state;
    return (
      <div className="  sm:max-w-4xl w-full p-10 sm:p-0 flex flex-col  flex-wrap space-y-6 bg-opacity-5 bg-gray-50 border-0 border-primary min-h-screen max-w-4xl items-center">
        <h1 className=" w-auto text-4xl font-black mt-4">加入我们</h1>

        <div className="flex flex-wrap flex-col sm:flex-row w-full max-w-2xl  justify-between items-center">
          <label className="label max-w-xs sm:max-w-2xl w-full">
            <span className="label-text">基本信息</span>
            <span className="label-text-alt">用于之后考核通知</span>
          </label>

          <input
            ref={(name) => {
              this.name = name;
            }}
            type="text"
            placeholder="姓名"
            className="input input-bordered input-primary w-full max-w-xs mt-4"
          />
          <input
            ref={(id) => {
              this.id = id;
            }}
            type="text"
            placeholder="学号"
            className="input input-bordered input-primary w-full max-w-xs mt-4"
          />
          <input
            ref={(email) => {
              this.email = email;
            }}
            type="text"
            placeholder="邮箱"
            className="input input-bordered input-primary w-full max-w-xs mt-4"
          />
          <input
            ref={(qq) => {
              this.qq = qq;
            }}
            type="text"
            placeholder="QQ"
            className="input input-bordered input-primary w-full max-w-xs mt-4"
          />
        </div>

        <div className="form-control w-full max-w-xs sm:max-w-2xl ">
          <label className="label">
            <span className="label-text">自我介绍</span>
            <span className="label-text-alt">可简述自己的特长、爱好等</span>
          </label>
          <textarea
            ref={(introduce) => {
              this.introduce = introduce;
            }}
            className="textarea textarea-primary h-40"
            placeholder="自我介绍"
          ></textarea>
        </div>

        <div className="flex flex-wrap flex-col sm:flex-row w-full max-w-2xl  justify-between items-center">
          <div className="divider w-full max-w-xs sm:max-w-2xl " />
          <div className="flex flex-col w-full max-w-xs">
            <div className=" form-control w-full max-w-xs mt-4 bg-primary bg-opacity-5 rounded-lg">
              <label className="label">
                <span className="label-text">编程基础</span>
                <span className="label-text-alt">给自己的编程能力打个分</span>
              </label>
              <span className="block px-2">
                <input
                  type="range"
                  min="0"
                  max="100"
                  onChange={this.changeCodeState("level")}
                  value={level}
                  className="range range-primary"
                  step="50"
                />
              </span>
              <div className="w-full flex justify-between justify-items-center text-xs px-2 ">
                <span>初学</span>
                <span>熟练</span>
                <span>精通</span>
              </div>
            </div>

            {/* <div className="divider" /> */}

            <div className="form-control w-full h-full max-w-xs mt-4 bg-primary bg-opacity-5 rounded-lg">
              <label className="label">
                <span className="label-text">方向</span>
                <span className="label-text-alt">选择感兴趣的方向</span>
              </label>
              <div className="form-control">
                <label className="label cursor-pointer">
                  <span className="label-text">软件</span>
                  <input
                    type="radio"
                    name="radio-dir"
                    onChange={() => {
                      this.changeCodeState("direction")({
                        target: { value: "软件" },
                      });
                    }}
                    checked={direction === "软件"}
                    className="radio radio-primary"
                  />
                </label>
              </div>
              <div className="form-control">
                <label className="label cursor-pointer">
                  <span className="label-text">硬件</span>
                  <input
                    type="radio"
                    name="radio-dir"
                    onChange={() => {
                      this.changeCodeState("direction")({
                        target: { value: "硬件" },
                      });
                    }}
                    checked={direction === "硬件"}
                    className="radio radio-primary"
                  />
                </label>
              </div>
            </div>
          </div>

          {/* 编程语言选择框 */}
          <div className="form-control w-full max-w-xs mt-4 bg-primary bg-opacity-5 rounded-lg">
            {/* <div className="divider" /> */}

            <label className="label">
              <span className="label-text">编程语言</span>
              <span className="label-text-alt">掌握了哪种编程语言</span>
            </label>

            <label className="label cursor-pointer">
              <span className="label-text">C/C++</span>
              <input
                type="checkbox"
                onChange={this.changeCodeLanguage("c")}
                checked={code.language.c}
                className="checkbox checkbox-primary"
              />
            </label>

            <label className="label cursor-pointer">
              <span className="label-text">Java</span>
              <input
                type="checkbox"
                onChange={this.changeCodeLanguage("java")}
                checked={code.language.java}
                className="checkbox checkbox-primary"
              />
            </label>

            <label className="label cursor-pointer">
              <span className="label-text">Python</span>
              <input
                type="checkbox"
                onChange={this.changeCodeLanguage("python")}
                checked={code.language.python}
                className="checkbox checkbox-primary"
              />
            </label>

            <input
              ref={(elseLang) => {
                this.elseLang = elseLang;
              }}
              type="text"
              placeholder="其他"
              className="input input-ghost w-full max-w-xs"
            />
          </div>
        </div>

        <div className="form-control w-full max-w-xs">
          <label className="label cursor-pointer">
            <span className="label-text">是否愿意加入管理层</span>
            <input
              type="checkbox"
              onChange={() => {
                this.setState({
                  form: { ...form, management: !this.state.form.management },
                });
              }}
              checked={form.management}
              className="toggle toggle-primary"
            />
          </label>
        </div>

        <button
          onClick={this.submit}
          className={
            this.state.s
              ? "btn btn-outline btn-loading w-full max-w-xs animate-bounce"
              : "btn btn-outline btn-primary w-full max-w-xs"
          }
        >
          提交
        </button>

        <div className="h-4"></div>

        <input
          type="checkbox"
          ref={(alertBox) => {
            this.alertBox = alertBox;
          }}
          id="isEmptyAlert"
          className="modal-toggle"
        />
        <div className="modal">
          <div className="modal-box">
            <h3
              ref={(alertTitle) => {
                this.alertTitle = alertTitle;
              }}
              className="font-bold text-lg"
            >
              还有未填完的内容
            </h3>
            <p
              ref={(alertInfo) => {
                this.alertInfo = alertInfo;
              }}
              className="py-4"
            ></p>
            <div className="modal-action">
              <label htmlFor="isEmptyAlert" className="btn">
                好的
              </label>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Register;
