import React, { useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";

export default function Login() {
  const url = "admin/login";
  const navigate = useNavigate();
  let usn = useRef();
  let usp = useRef();

  useEffect(() => {
    document.title = "登陆-SAI创新基地";
    return () => {};
  }, []);

  const submit = () => {
    const json = {
      username: usn.current.value,
      password: usp.current.value,
    };
    console.log(JSON.stringify(json));
    let myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    fetch(url, {
      method: "POST",
      body: JSON.stringify(json),
      headers: myHeaders,
    })
      .then((res) => {
        console.log(res);
        let tokenInfo = res.headers.get("Authorization");
        // console.log(tokenInfo);
        localStorage.setItem("token", tokenInfo);
        // console.log(res.headers);
        return res.text();
      })
      .then((text) => {
        console.log(text);
        if (text === "success") navigate("/queryList");
        else alert(text);
      })
      .catch((err) => {
        alert("登陆失败，请重试");
        console.log(err);
      });
  };

  return (
    <div className="  font-black w-full h-screen flex flex-col items-center justify-center">
      <div className=" w-80 h-64 p-4 flex flex-col items-center justify-around bg-gray-100 bg-opacity-10 rounded-2xl  ">
        <div className=" text-2xl">SAI后台登陆</div>
        <input
          type="text"
          ref={usn}
          placeholder="用户名"
          className="input input-bordered input-primary w-full max-w-xs "
        />
        <input
          type="password"
          ref={usp}
          placeholder="密码"
          className="input input-bordered input-primary w-full max-w-xs"
        />
        <button
          onClick={submit}
          className=" text-xl btn btn-outline btn-primary w-full max-w-xs"
        >
          登陆
        </button>
      </div>
    </div>
  );
}
