import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
// import axios from "axios";
import Tr from "../../components/Tr";

export default function Query() {
  const queryURL = "/query";
  let navigate = useNavigate();

  // const p = {
  //   id: 2101630301,
  //   name: '姓名',
  //   email: '123456@qq.com',
  //   qq: '123456',
  //   introduce: 'wqe',
  //   level: 50,
  //   direction: "硬件",
  //   code: [
  //     'c','java','python',
  //   ],
  //   else: 'elselang',
  //   management: true,
  // }

  const [queryData, setqueryData] = useState([
    {
      name: "无数据",
      info: {
        language: [],
      },
    },
  ]);

  useEffect(() => {
    document.title = "查询-SAI创新基地";
    if (localStorage.token === undefined) {
      alert("请先登陆");
      navigate("/login", { replace: true });
    } else getData();
    return () => {};
  }, []);

  function getData() {
    const token = localStorage.token;
    // let myHeaders = new Headers();
    // myHeaders.append("Authorization", token);
    // console.log(myHeaders);
    fetch(queryURL, {
      credentials: "include",
      headers: {
        Authorization: token,
      },
      method: "GET",
      // myHeaders,
    })
      .then((res) => {
        res.json().then((json) => {
          setqueryData(json);
        });
      })
      .catch((err) => {
        alert("网络出现问题，请稍后重试(多次重试无效请重新登陆)");
        console.log(err);
        localStorage.clear("token");
        window.location.reload();
      });
  }

  return (
    <div className="">
      <div
        className="overflow-x-auto block w-screen p-3 min-h-screen"
        style={{ "word-break": "keep-all" }}
      >
        <table className=" table-compact w-full">
          <thead>
            <tr>
              <th>学号</th>
              <th>姓名</th>
              <th>邮箱</th>
              <th>QQ</th>
              <th>自我介绍</th>
              <th>编程基础</th>
              <th>方向</th>
              <th>编程语言</th>
              <th>管理层意向</th>
            </tr>
          </thead>
          <tbody>
            {queryData.map((item, index) => (
              <Tr key={index} {...queryData[index]} />
            ))}
          </tbody>
          {/* <tfoot>
      <tr>
        <th></th> 
        <th>Name</th> 
        <th>Job</th> 
        <th>company</th> 
        <th>location</th> 
        <th>Last Login</th> 
        <th>Favorite Color</th>
      </tr>
    </tfoot> */}
        </table>
      </div>
    </div>
  );
}
